<template>
    <div
        v-show="show"
        v-on="$listeners"
        class="gray-mask">
        <slot/>
    </div>
</template>
<script>
export default {
    name: 'GrayMask',
    props: {
        show: {
            type: [String, Boolean],
            default: '',
        },
    },
    methods: {
        stopSlide(e) {
            e.preventDefault();
        },
    },
    watch: {
        show: {
            handler(v) {
                if (v) {
                    console.log('禁止滑动');
                    document.body.addEventListener('touchmove', this.stopSlide, { passive: false });
                } else {
                    console.log('可以滑动');
                    document.body.removeEventListener('touchmove', this.stopSlide, { passive: false });
                }
            },
            immediate: true,
        },
    },
    destroyed() {
        document.body.removeEventListener('touchmove', this.stopSlide, { passive: false });
    },

};
</script>

<style scoped>
.gray-mask {
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    width: 100vw;
    height: 100vh;
    z-index: 9999;
}
</style>

