<template>
    <div class="share">
        <img
            class="logo"
            :src="config.default.logo">
        <div class="title" />
        <div class="card">
            <div class="icon">
                <img :src="info.headIcon || config.default.userIcon">
            </div>
            <p>邀你下载想看</p>
            <p>一起看资讯挣话费</p>
            <div class="code">{{ showUser }}的邀请码：{{ showInvitationCode }}</div>
        </div>
        <div class="steps">
            <h2>快速领红包攻略</h2>
            <p>第一步：点击下方按钮下载想看APP</p>
            <p>第二步：打开登录，领高额新人红包</p>
            <button
                @click="download"
                class="scale-infinite">前往下载</button>
        </div>
        <gray-mask :show='login'>
            <div class="dialog">
                <div
                    class="close"
                    @click="login=false"/>
                <p>允许微信登录后可自动绑定好友关系</p>
                <button @click="auth()">去登录</button>
            </div>
        </gray-mask>
    </div>
</template>

<script>
import config from '@/config';
import { invitationShare, invitationPreinvited } from '@/api/invitation';
import GrayMask from '@/components/GrayMask';
import { wechatAuth, goAppStore } from '@/utils/share';
import { copy } from '@/utils/string';

export default {
    components: {
        GrayMask,
    },
    data() {
        return {
            config,
            invitationCode: this.$route.query.shareId || 'XIANGKAN',
            login: true,
            info: {
                user: '想看小助手',
            },
        };
    },
    computed: {
        showInvitationCode() {
            if (this.$route.query.share === 'partner') {
                return this.invitationCode;
            }
            return this.invitationCode.replace(/^.*?#/, '');
        },
        showUser() {
            const label = this.info.user.length <= 6
                ? this.info.user
                : `${this.info.user.substring(0, 6)}...`;

            return label;
        },
        shareChannel() {
            if (this.$route.query.share) {
                return this.$route.query.share;
            }
            if (this.showInvitationCode === 'XIANGKAN') {
                return 'news';
            }
            return 'invite';
        },
    },
    created() {
        if (this.$route.query.code) {
            this.login = false;
            this.submitPreinvited();
        } else if (this.$route.query.state) {
            this.$o2o({
                e_c: '邀请分享页',
                e_a: '曝光',
                e_n: '引导授权弹窗',
                e_x: {
                    share_channel: this.shareChannel,
                },
            });
        } else {
            this.auth(true);
        }
        invitationShare({ invitationCode: this.invitationCode }).then((res) => {
            if (res.data.status === 200) {
                this.info = res.data.data;
                this.$o2o({
                    e_c: '邀请分享页',
                    e_a: '曝光',
                    e_n: '邀请码分享页面',
                    e_x: {
                        share_channel: this.shareChannel,
                    },
                });
                this.$root.hidePlaceholder();
            } else {
                this.$error();
            }
        }).catch(this.$error);
    },
    methods: {
        auth(isAuto) {
            if (!isAuto) {
                this.$o2o({
                    e_c: '邀请分享页',
                    e_a: '点击',
                    e_n: '去登陆授权',
                    e_x: {
                        share_channel: this.shareChannel,
                    },
                });
            }
            this.login = false;
            wechatAuth(window.location.href);
        },
        download() {
            this.$o2o({
                e_c: '邀请分享页',
                e_a: '点击',
                e_n: '前往下载',
                e_x: {
                    share_channel: this.shareChannel,
                },
            });
            copy(this.invitationCode);
            // 地推合伙人的ck区别于其他活动，通过query中share判断
            if (this.$route.query.share === 'partner') {
                goAppStore('', 'CK1433782967648');
            } else {
                goAppStore('', 'CK1429645648411');
            }
        },
        submitPreinvited() {
            invitationPreinvited({
                invitationCode: this.invitationCode,
                type: 'wechat',
                key: this.$route.query.code,
                channel: this.$route.query.share,
                token: this.$route.query.token,
            });
        },
    },
};
</script>

