import request, { apiResError } from './request';

// https://wiki.n.miui.com/pages/viewpage.action?pageId=132810374
// 收徒分享页
export function invitationShare(params) {
    return request.get('/api/v4/invitation/share', params)
        .then((response) => {
            if (response.data.status !== 200) {
                apiResError(response);
            }
            return Promise.resolve(response);
        });
}

