// 面对面扫码红包 接口文档：https://wiki.n.miui.com/pages/viewpage.action?pageId=201723856
// 面对面扫码红包->领取红包记录
export function packetRecords(params) {
    return request.get('/api/v4/invitation/f2f/packetRecords', params)
        .then((response) => {
            if (response.data.status !== 200) {
                apiResError(response);
                return Promise.reject(response.data.data);
            }
            return Promise.resolve(response.data.data);
        });
}

